<template>
  <div class="set-commodify">
    <div class="content_box">
      <div class="title">
        写入商品
      </div>
      <div class="form">
        <el-form :model="ruleForm" status-icon ref="ruleForm" label-width="100px" class="demo-ruleForm">

          <el-form-item label="商品分类">
            <el-input type="text" v-model="ruleForm.commodity_classify"></el-input>
          </el-form-item>

          <el-form-item
            v-for="(domain, index) in ruleForm.commodity_advertising_list"
            :label="'广告商品' + (index + 1)"
            :key="index + 100"
          >
            <el-input type="text" v-model="domain.commodity_image" placeholder="广告封面"></el-input>
            <el-input type="text" v-model="domain.commodity_price" placeholder="广告价格"></el-input>
            <el-input type="text" v-model="domain.commodity_title" placeholder="广告标题"></el-input>
            <el-input type="text" v-model="domain.commodity_evaluate_number" placeholder="评价数量"></el-input>
          </el-form-item>

          <el-form-item>
            <el-button @click="addCommodifyAdvertising">新增商品</el-button>
          </el-form-item>


          <el-form-item
            v-for="(domain, index) in ruleForm.commodity_exhibition_list"
            :label="'商品' + (index + 1)"
            :key="index"
          >
            <el-input type="text" v-model="domain.commodity_image" placeholder="商品封面"></el-input>
            <el-input type="text" v-model="domain.commodity_price" placeholder="商品价格"></el-input>
            <el-input type="text" v-model="domain.commodity_title" placeholder="商品标题"></el-input>
            <el-input type="text" v-model="domain.commodity_evaluate_number" placeholder="评价数量"></el-input>
            <el-input type="text" v-model="domain.company_name" placeholder="公司名称"></el-input>
            <el-input type="text" v-model="domain.commodity_brand" placeholder="品牌"></el-input>
            <el-input type="text" v-model="domain.commodity_type" placeholder="类型"></el-input>
          </el-form-item>

          <el-form-item>
            <el-button @click="addCommodify">新增商品</el-button>
          </el-form-item>


          <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
            <el-button @click="resetForm('ruleForm')">重置</el-button>
          </el-form-item>

        </el-form> 
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      ruleForm: {
        commodity_advertising_list: [
          {
            commodity_image: '',
            commodity_price: '',
            commodity_title: '',
            commodity_evaluate_number: ''
          }
        ],
        commodity_exhibition_list: [
          {
            commodity_image: '',
            commodity_price: '',
            commodity_title: '',
            commodity_evaluate_number: '',
            company_name: '',
            commodity_brand: ''
          }
        ]
      }
    }
  },
  methods: {
    submitForm () {
      console.log('commodifyInfo ==> ', this.ruleForm)
      this.$alert('生成成功，请在控制台查看!', '提示', {
        confirmButtonText: '确定'
      });
    },
    resetForm () {
      this.ruleForm = {
        commodity_advertising_list: [
          {
            commodity_image: '',
            commodity_price: '',
            commodity_title: '',
            commodity_evaluate_number: ''
          }
        ],
        commodity_exhibition_list: [
          {
            commodity_image: '',
            commodity_price: '',
            commodity_title: '',
            commodity_evaluate_number: '',
            company_name: '',
            commodity_brand: ''
          }
        ]
      }
    },
    addCommodifyAdvertising () {
      this.ruleForm.commodity_advertising_list.push({
        commodity_image: '',
        commodity_price: '',
        commodity_title: '',
        commodity_evaluate_number: ''
      })
    },
    addCommodify () {
      this.ruleForm.commodity_exhibition_list.push({
        commodity_image: '',
        commodity_price: '',
        commodity_title: '',
        commodity_evaluate_number: '',
        company_name: '',
        commodity_brand: ''
      })
    }
  }
};
</script>

<style lang="less" scoped>
.set-commodify {
  .content_box {
    width: 70%;
    height: 100%;
    margin: 10px auto;
    .title {
      width: 100%;
      height: 100px;
      font-size: 24px;
      font-weight: bold;
      text-align: center;
      line-height: 100px;
    }
    .form {
      text-align: center;
      padding: 10px 30px;
    }
  }
}
</style>