<template>
  <div class="commodify-info">
    <h2>写入商品详情</h2>
    <el-form :model="ruleForm" ref="ruleForm" label-width="160px" class="demo-ruleForm">
      <el-form-item label="发货地">
        <el-input type="text" v-model="ruleForm.place_of_dispatch"></el-input>
      </el-form-item>

      <el-form-item label="公司头像">
        <el-input type="text" v-model="ruleForm.company_image"></el-input>
      </el-form-item>

      <!-- <el-form-item label="商品名称">
        <el-input type="text" v-model="ruleForm.commodify_name"></el-input>
      </el-form-item> -->

      <el-form-item label="商品价格">
        <el-input type="text" v-model="ruleForm.commodify_price"></el-input>
      </el-form-item>

      <!-- <el-form-item label="商品编号">
        <el-input type="text" v-model="ruleForm.commodify_serial_number"></el-input>
      </el-form-item> -->

      <el-form-item label="店铺名称">
        <el-input type="text" v-model="ruleForm.store_name"></el-input>
      </el-form-item>

      <el-form-item label="好评率">
        <el-input type="text" v-model="ruleForm.reputation"></el-input>
      </el-form-item>

      <!-- <el-form-item label="品牌">
        <el-input type="text" v-model="ruleForm.brand"></el-input>
      </el-form-item>

      <el-form-item label="类型">
        <el-input type="text" v-model="ruleForm.type"></el-input>
      </el-form-item> -->

      <el-form-item v-for="(item, idx) in ruleForm.specification_parameter_list" :key="idx" :label="'规格参数' + (idx + 1)">
        <el-input type="text" v-model="item.key"></el-input>
        <el-input type="text" v-model="item.value"></el-input>
      </el-form-item>

      <el-form-item>
        <el-button @click="addSpecification">新增规格参数</el-button>
      </el-form-item>

      <el-form-item v-for="(item, idx) in ruleForm.products_introduction_list" :key="idx" :label="'商品详情' + (idx + 1)">
        <el-input type="text" v-model="item.imageUrl"></el-input>
      </el-form-item>

      <el-form-item>
        <el-button @click="addCompanyImage">新增商品介绍图片</el-button>
      </el-form-item>

      <el-form-item v-for="(item, idx) in ruleForm.evaluate_tab_list" :key="idx + 100" :label="'店铺评价标签' + (idx + 1)">
        <el-input type="text" v-model="item.tab"></el-input>
      </el-form-item>

      <el-form-item>
        <el-button @click="addEvaluateTab">新增店铺评价标签</el-button>
      </el-form-item>

      <el-form-item v-for="(item, idx) in ruleForm.evaluate_content_list" :key="idx + 1000" :label="'店铺评价内容' + (idx + 1)">
        <el-input type="text" v-model="item.content" placeholder="内容"></el-input>
        <el-input type="text" v-model="item.username" placeholder="用户名称"></el-input>
        <el-input type="text" v-model="item.values" placeholder="评分"></el-input>
        <el-input type="text" v-model="item.create_at" placeholder="时间"></el-input>
      </el-form-item>

      <el-form-item>
        <el-button @click="addEvaluateContent">新增店铺评价内容</el-button>
      </el-form-item>

      <el-form-item v-for="(item, idx) in ruleForm.commodify_image_list" :key="idx + 1000" :label="'商品详情图片' + (idx + 1)">
        <el-input type="text" v-model="item.image" placeholder="内容"></el-input>
      </el-form-item>

      <el-form-item>
        <el-button @click="addCommodifyImage">新增店铺评价内容</el-button>
      </el-form-item>

      <el-form-item v-for="(item, idx) in ruleForm.hot_list" :key="idx + 1000" :label="'热销商品' + (idx + 1)">
        <el-input type="text" v-model="item.hot_image" placeholder="内容"></el-input>
        <el-input type="text" v-model="item.hot_number" placeholder="内容"></el-input>
        <el-input type="text" v-model="item.hot_price" placeholder="内容"></el-input>
      </el-form-item>

      <el-form-item>
        <el-button @click="addHot">新增热销商品</el-button>
      </el-form-item>

      <el-form-item>
        <el-button @click="handlerSubmit">提交</el-button>
      </el-form-item>
    </el-form>

    <div class="animate_demo">
      <div class="roll">
      <ul :class="{marquee_top:animate}">
        <li v-for="(item, index) in msg" :key="index">
        <span class="txtWrap">
          <span class="txt">{{item.name}}抢得商品{{item.goods}}</span>
          <span class="txt">已有123人申请</span>
        </span>
        </li>
      </ul>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        ruleForm: {
          products_introduction_list: [
            {
              imageUrl: ''
            }
          ],
          evaluate_tab_list: [
            {
              tab: ''
            }
          ],
          evaluate_content_list: [
            {
              content: '',
              username: '',
              values: '',
              create_at: ''
            }
          ],
          commodify_image_list: [
            {
              image: ''
            }
          ],
          hot_list: [
            {
              hot_image: '',
              hot_number: '',
              hot_price: ''
            }
          ],
          specification_parameter_list: [
            {
              key: '',
              value: ''
            }
          ]
        },

        msg: [
          {
          name: '张**',
          goods: '牙膏'
          },
          {
          name: '王**',
          goods: '牙刷'
          },
          {
          name: '钟**',
          goods: '肥皂'
          }
        ],
        animate: false,
        setInTime:'' // 定时器
      }
    },
    mounted () {
      this.setInTime = setInterval(this.showMarquee, 3000)
    },
    destroyed () {
      clearInterval(this.setInTime) // 页面销毁时清除定时器
    },
    methods: {
      showMarquee () {
        this.animate = true
        setTimeout(() => {
          this.msg.push(this.msg[0])
          this.msg.shift()
          this.animate = false
        }, 500)
      },
      addHot () {
        this.ruleForm.hot_list.push({
              hot_image: '',
              hot_number: '',
              hot_price: ''
            })
      },
      addCommodifyImage () {
        this.ruleForm.commodify_image_list.push({
          image: ''
        })
      },
      addCompanyImage () {
        this.ruleForm.products_introduction_list.push({
          imageUrl: ''
        })
      },
      addEvaluateTab () {
        this.ruleForm.evaluate_tab_list.push({
          tab: ''
        })
      },
      addEvaluateContent () {
        this.ruleForm.evaluate_content_list.push({
          content: ''
        })
      },
      addSpecification () {
        this.ruleForm.specification_parameter_list.push({
          key: '',
          value: ''
        })
      },
      handlerSubmit () {
        console.log('商品详情json ==> ', this.ruleForm);
        this.$alert('生成成功，请在控制台查看!', '提示', {
        confirmButtonText: '确定'
      });
      }
    }
  }
</script>

<style lang="less" scoped>
.commodify-info {
  width: 80%;
  margin: 20px auto;
  h2 {
    display: block;
    width: 100%;
    height: 100px;
    font-size: 24px;
    font-weight: bold;
    text-align: center;
    line-height: 100px;
  }
}

.animate_demo {
  width: 100%;
  height: 200px;
  border: 1px solid black;
}

.marquee_top {
   transition: all 0.5s;
   margin-top: -26px; /* 容器高度 */
}
</style>